import {OnInit, Component} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { UserService } from './../../../shared/services/index';
import {CompanyService} from './../../../shared/services/company.service';


declare var jQuery:any;
@Component({
  selector: 'og-single-user',
  template: require('./single-user.component.html'),
  styles: [require('./single-user.component.css'), require('./../../ionicons.min.css'),require('./../../../../assets/css/sahil-hover.css'), require('./../../../../assets/css/custom-material.css')],
})

export class SingleUserComponent implements OnInit{
  id: any;
  name : string;
  companies : any[];
  loading :boolean = false;
  constructor(
    private route: ActivatedRoute,
    private userService: UserService,
    private _companyService: CompanyService
    ) {
       this.route.params.subscribe(params => {
       this.id = params['id'];
      });
      
  }

  ngOnInit() {
    this.loading = true;
    this._companyService.getUserCompanies(this.id).subscribe((result)=>{
      this.companies = result;
      this.loading = false;
    });
  }

  

}



